* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.wrapper {
  max-width: 1920px;
  margin: 0 auto;
}

.top-media {
  position: fixed;
  z-index: 1;
  display: flex;
  width: 100%;
  max-width: 1920px;
  padding: 5px 0;
  background-color: white;
  box-shadow: 0 3px 3px #999;
}
.top-media__link {
  font-size: 35px;
  padding: 5px 30px 5px 0;
  color: #444;
}
@media (max-width: 799px) {
  .top-media {
    font-size: 30px;
    padding: 3px 0;
  }
}
.top-media__slogan {
  display: block;
  align-self: center;
  padding-left: 25%;
  font-family: "Dancing Script", cursive;
  font-size: 25px;
  flex-grow: 1;
}
@media (max-width: 799px) {
  .top-media__slogan {
    padding-left: 5%;
    font-size: 15px;
  }
}

.menu {
  box-shadow: 0 3px 2px #999;
  padding: 20px 0;
}
.menu__list {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  list-style: none;
}
@media (max-width: 799px) {
  .menu__list {
    position: fixed;
    z-index: 2;
    top: 0;
    left: 100%;
    height: 100%;
    flex-direction: column;
    background-color: white;
    opacity: 0;
    padding-top: 70px;
    padding-bottom: 50px;
    transition: 0.3s;
  }
}

.menu__list--section {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-evenly;
  align-self: center;
}

.menu--section {
  display: flex;
  box-shadow: none;
  flex-grow: 1;
}

.menu__element {
  display: flex;
  width: 200px;
  align-items: center;
}
@media (max-width: 799px) {
  .menu__element {
    width: 80%;
  }
}

.menu__link {
  flex-basis: 100%;
  display: flex;
  height: 70px;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: white;
  text-decoration: none;
  color: #333;
  border: 2px solid #444;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: arial;
  transition: 0.2s;
  padding: 15px 20px;
}
@media (max-width: 799px) {
  .menu__link {
    border: none;
    border-bottom: 1px solid #444;
    margin: 0 15px;
  }
}
.menu__link:hover {
  /* font-weight: 900; */
  background-color: #444;
  color: white;
}

.menu__link--section {
  width: 100%;
  margin-bottom: 20px;
}

.menu__close {
  display: none;
}
@media (max-width: 799px) {
  .menu__close {
    display: block;
    position: absolute;
    top: 7%;
    right: 25%;
    font-size: 40px;
    cursor: pointer;
    color: #444;
    padding: 4px 7px;
    border-radius: 15%;
    background-color: rgba(153, 153, 153, 0.4);
  }
}

.header {
  height: 400px;
  position: relative;
}
.header__logo {
  position: absolute;
  margin-top: 25px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 350px;
}
.header__menu {
  display: none;
}
@media (max-width: 799px) {
  .header__menu {
    display: block;
    position: fixed;
    background-color: rgba(153, 153, 153, 0.4);
    top: 100px;
    padding: 4px;
    color: #444;
    border-radius: 20%;
    right: 25px;
    font-size: 30px;
    cursor: pointer;
  }
}

.sections {
  display: flex;
  width: 100%;
  height: 400px;
  overflow: hidden;
  justify-content: space-between;
  margin-bottom: 20px;
}

.contener {
  z-index: -3;
  display: flex;
  overflow: hidden;
}

.section-contener {
  display: flex;
  min-width: 50%;
  flex-grow: 1;
  justify-content: center;
}

.section-information {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 450px;
  text-align: center;
}
@media (max-width: 799px) {
  .section-information {
    width: 200px;
  }
}

div.motion-div {
  display: flex;
  width: 100%;
}

.footer {
  padding: 5px 0px 5px 30px;
  background-color: #333;
  color: white;
}

.active {
  background-color: #444;
  color: white;
}/*# sourceMappingURL=App.css.map */